$previewWidth: 100px;

.root {
    overflow: hidden;
    width: $previewWidth;
    margin: 0 auto;
    border-radius: 10px;
    box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.08);
    flex: 0 0 $previewWidth;

    .offscreen {
        position: absolute;
        width: 0;
        height: 0;
        overflow: hidden;
    }

    .sample {
        pointer-events: none;
    }
}

.insert {
    position: relative;
    width: $previewWidth;
    min-height: 135px;

    img {
        display: block;
        width: 100% !important;
    }
}
